      document.getElementById("QS").style.backgroundColor = "white";
      document.getElementById("QS").style.color = "grey";
      document.getElementById("QH").style.backgroundColor = "white";
      document.getElementById("QH").style.color = "grey";
      document.getElementById("A").style.backgroundColor = "#0090ff";
      document.getElementById("A").style.color = "white";
      this.$router.push("/team");
    }
  }
};
</script>

