<template>
        <v-row>
            <v-col cols='12' md='6' v-bind:style="{'margin-top': '2%'}">
                <h2 class='titleContac' v-bind:style="{'font-size': '35px !important'}">
                    CONTÁCTANOS
                </h2>
            </v-col>
            <v-col cols='12' md='6'>
            </v-col>
            <v-col cols='12' md='4'>
            </v-col>
            <v-col cols='12' md='8'>
                <v-container
                    class='cardCont'
                    max-width="90%"
                    outlined
                    >
                    <v-row cols= '12'  md='7' sm='12' xs='12' >
                        <v-col v-bind:style="{'margin': '4%'}">
                            <p class="datosT" > Deja tus datos</p>
                                    <v-row>
                                        <v-col cols='12' md= '6' v-bind:style="{'padding-top':'5px'}">
                                            <v-text-field
                                                outlined
                                                dense
                                                v-model="nombre"
                                                :required= rules.required
                                                :rules="[rules.required, rules.min, rules.text]"
                                                label="Nombre *"
                                                hide-details="auto"
                                            ></v-text-field>
                                        </v-col>
                                        <v-col cols='12' md= '6' v-bind:style="{'padding-top':'5px'}">
                                            <v-text-field
                                                outlined
                                                dense
                                                v-model="telefono"
                                                :rules="[rules.required, rules.minNu, rules.number]"
                                                label="Teléfono *"
                                                hide-details="auto"
                                            ></v-text-field>
                                        </v-col>
                                        <v-col cols='12' md= '12' v-bind:style="{'padding-top':'5px'}">
                                        <v-text-field
                                                outlined
                                                v-model="correo"
                                                :rules="[rules.email]"
                                                label="Correo electrónico"
                                                hide-details="auto"
                                            ></v-text-field>
                                        </v-col>
                                        <v-col cols='12' md= '12' v-bind:style="{'padding-top':'5px'}">
                                            <v-textarea
                                                outlined
                                                dense
                                                rows="2"
                                                v-model="necesidad"
                                                label="Cúentanos tus necesidades..."
                                                hide-details="auto"
                                            ></v-textarea>
                                        </v-col>
                                        <v-container v-bind:style="{'text-align': 'end'}" >
                                            <v-btn
                                            @click="enviar"
                                            color='#008dff'
                                            class="btnContacto"
                                            >
                                            Enviar
                                            </v-btn>
                                </v-container>
                                    </v-row> 

                        </v-col>
                        <v-col  v-if="$vssWidth >= 650" cols= '12'  md='5' v-bind:style="{'padding': '0px'}">
                            <v-container class='contacto' >
                                <v-container class='down'>
                                    <v-col >
                                        <v-row >
                                            <v-icon
                                            color="white">
                                                mdi-email
                                            </v-icon>
                                            <h4 class="conteCall l"> Escríbenos</h4>
                                        </v-row>
                                            <h4 class="conteCall">     soporte@capitalbranch.org</h4>
                                    </v-col>
                                    <v-col>
                                        <v-row>
                                            <v-icon
                                                color="white">
                                                mdi-phone
                                            </v-icon>
                                            <h4 class="conteCall l"> Llámanos</h4>
                                        </v-row>
                                            <h4 class="conteCall">  315 584 6857 - 312 468 4588</h4>
                                    </v-col>
                                </v-container>
                            </v-container>
                        </v-col>
                    </v-row>
                </v-container>
            </v-col>
        </v-row>
</template>

<script>
import { host} from './Tools/variables'
import axios from 'axios'
export default {
    data() {
        return {
            necesidad: null, 
            telefono: null, 
            nombre: null, 
            correo: null, 
            rules: {
                required: value => 
                     !!value || 'Campo requerido.',
                min: v => { 
                    v.length > 2 || 'Invalido'},
                minNu: v => { 
                    v.length > 5 || 'Invalido'},
                text: v => {
                    const req = /^[\w'\-,.][^0-9_!¡?÷?¿/\\+=@#$%ˆ&*(){}|~<>;:[\]]{2,}$/
                    return req.test(v) || 'Solo letras'
                },
                number: v => {
                    const req = /^[0-9 ]*$/
                    return req.test(v) || 'solo numeros'
                },
                email: v =>{
                    const req = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
                    return req.test(v) || 'formato invalido para el correo'
                }
            },
        }
    },
    methods: {
        enviar(){
            if(this.nombre == null || this.telefono == null){
                this.$alert('Debes agregar al menos tu nombre y un teléfono')
            }else{
                const fd = new FormData();
                    fd.append('mail', this.correo);
                    fd.append('phone', this.telefono);
                    fd.append('text', this.necesidad);
                    fd.append('name', this.nombre);
                    axios.post(host, fd).then((result) => {                
                    let data = result.data.mensaje;
                        if(data == 'OK'){
                            this.$alert('Hemos enviado tu solicitud, te estaremos contactando lo antes posible')  
                            this.nombre = null
                            this.telefono = null
                            this.necesidad = null
                            this.correo = null  
                        }
                        else{
                            this.$$alert('Error!, intenta enviar la solicitud nuevamente')
                        }
                }) 
            }
        }  
    },

}
</script>

