<template>
  <v-app id="fondo">
    <template v-if="$vssWidth >= 650" id="filter">
      <ToolBar />
    </template>
    <template v-if="$vssWidth <= 650" class="filterS">
      <menu-bar />
    </template>
    <v-content v-bind:style="{'margin-top':'0%'}">
      <transition name="slide-fade">
        <router-view></router-view>
      </transition>
    </v-content>
    <Footer />
  </v-app>
</template>

<script>
import ToolBar from "./toolBar.vue";
import Footer from "./footer.vue";
import MenuBar from "./menuBar.vue";

export default {
  name: "Esquema",
  components: {
    ToolBar,
    Footer,
    MenuBar
  },
  data() {
    return {
      route: null
    };
  },
  mounted() {
    this.route = this.$route.name;
    this.changeImg();
  },
  updated() {
    this.route = this.$route.name;
    this.changeImg();
  },
  methods: {
    changeImg() {
      if (this.$vssWidth <= 650) {
        document.getElementById("fondo").style.backgroundImage = "none";
      } else {
        document.getElementById("fondo").style.backgroundRepeat = "no-repeat";
        document.getElementById("fondo").style.backgroundSize = "100%";
        // document.getElementById("fondo").style.backgroundColor = '#02539e8f';
        if (this.route == "Qhacemos") {
          document.getElementById("fondo").style.backgroundImage =
            "url(" + require("../assets/fondoNosotros.jpg") + ")";
        }
        if (this.route == "Productos") {
          document.getElementById("fondo").style.backgroundImage =
            "url(" + require("../assets/fondoProductos.jpg") + ")";
        }
        if (this.route == "Inicio") {
          document.getElementById("fondo").style.backgroundImage =
            "url(" + require("../assets/fondo1.jpg") + ")";
        }
        if (this.route == "Qsomos") {
          document.getElementById("fondo").style.backgroundImage =
            "url(" + require("../assets/fondoSomos.jpg") + ")";
        }
        if (this.route == "Equipo") {
          document.getElementById("fondo").style.backgroundImage =
            "url(" + require("../assets/fondoEquipo.jpg") + ")";
        }
        if (this.route == "Contacto") {
          document.getElementById("fondo").style.backgroundImage =
            "url(" + require("../assets/fondoContacto.jpg") + ")";
        }
      }
    }
  }
};
</script>

<style>
#fondo {
  background-color: #0a1d45;
}
#filter {
  background-color: #02539e8f !important;
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
}

/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.3s ease;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

.filterS {
  background-color: #02539e8f !important;
  background-image: none !important;
}
</style>