import Vue from 'vue'
import Router from 'vue-router'
import Nosotros from '../../components/nosotros.vue'
import Inicio from '../../components/inicio.vue'
import Productos from '../../components/productos.vue'
import Contacto from '../../components/contacto.vue'
import Qsomos from '../../components/nosotros/who.vue'
import Qhacemos from '../../components/nosotros/what.vue'
import Team from '../../components/nosotros/team.vue'



Vue.use(Router)
const router = new Router({
    routes : [
        {
            path: '/',
            name: 'Inicio',
            component: Inicio,
        },
        {
            path: '/nosotros',
            name: 'Nosotros',
            component: Nosotros,
            children:[
                {
                path:'/somos',
                name: 'Qsomos',
                component: Qsomos
                },
                {
                    path:'/hacemos',
                    name: 'Qhacemos',
                    component: Qhacemos
                },
                {
                    path:'/team',
                    name: 'Equipo',
                    component: Team
                },
                
                
            ]
        },
        {
            path: '/productos',
            name: 'Productos',
            component: Productos,
        },
        {
            path: '/contacto',
            name: 'Contacto',
            component: Contacto,
        }
    ]
});

export default router
