<template>
  <v-row v-bind:style="{'margin-top': '2%'}">
    <v-col cols="12" md="5">
      <v-row>
        <v-col v-for="data in categories" v-model="categoria" :key="data">
          <v-btn plain @click="getAliados(data)" class="btnBig">
            <v-container class="linea">{{data}}</v-container>
            <v-icon color="white" large>mdi-play</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-col>
    <v-col
      v-bind:style="{'text-align': 'center !important' }"
      v-if="noRow != 0 && noCol != 0"
      cols="12"
      md="7"
    >
      <v-row v-for="dataR in noRow" :key="dataR">
        <v-col v-for="dataC,i  in noCol" :key="dataC">
          <div>
            <v-row v-bind:style="{'text-align': 'center !important' }">
              <v-container>
                <v-avatar size="250">
                  <transition name="fade">
                    <img :alt="aliadosByC[i].nombre" v-bind:src="aliadosByC[i].data" />
                  </transition>
                </v-avatar>
              </v-container>
            </v-row>

            <span class="nombreAliados">{{aliadosByC[i].nombre}}</span>
          </div>
        </v-col>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "Team",
  data() {
    return {
      categories: [
        "GOBIERNO",
        "CIENCIA Y TECNOLOGÍA",
        "EDUCACIÓN Y FORMACIÓN",
        "SALUD, BIENESTAR Y BELLEZA"
      ],
      aliados: [
        {
          nombre: "SALUD, BIENESTAR Y BELLEZA",
          data: [
            { img: require("../../assets/Logos/sura.jpg"), nombre: "Sura" },
            {
              img: require("../../assets/Logos/aseoServicios.jpg"),
              nombre: "Aseo Servicios SAS"
            },
            {
              img: require("../../assets/Logos/organic.png"),
              nombre: "Organic Nails"
            },
            {
              img: require("../../assets/Logos/alianza.jpg"),
              nombre: "Alianza Mensajería"
            }
          ]
        },
        {
          nombre: "GOBIERNO",
          data: [
            {
              img: require("../../assets/Logos/appsco.png"),
              nombre: "Apps.co"
            },
            { img: require("../../assets/Logos/minTic.jpg"), nombre: "MinTic" }
          ]
        },
        {
          nombre: "CIENCIA Y TECNOLOGÍA",
          data: [
            {
              img: require("../../assets/Logos/suricataLabs.jpg"),
              nombre: "Suricata Labs"
            },
            {
              img: require("../../assets/Logos/absoluta.jpg"),
              nombre: "Absoluta"
            },
            {
              img: require("../../assets/Logos/dahuaMayorista.jpg"),
              nombre: "DahuaMayorista"
            }
          ]
        },
        {
          nombre: "EDUCACIÓN Y FORMACIÓN",
          data: [
            {
              img: require("../../assets/Logos/eduExpert.jpeg"),
              nombre: "EduExpert"
            }
          ]
        }
      ],
      noRow: 0,
      noCol: 0,
      categoria: null,
      aliadosByC: []
    };
  },
  methods: {
    getAliados(data) {
      this.aliadosByC = [];
      this.aliados.forEach(element => {
        console.log(element.data[0]["img"]);
        if (element.nombre == data) {
          this.noRow = 0;
          this.noCol = 0;
          for (let i = 0; i < element.data.length; i++) {
            const el = element.data[i];
            this.aliadosByC.push({ data: el["img"], nombre: el["nombre"] });
          }
          this.noRow = parseInt(this.aliadosByC.length / 4);
          if (this.noRow == 0) {
            this.noRow = 1;
          }
          this.noCol = this.aliadosByC.length;
        }
      });
    }
  }
};
</script>
